.popup__container {
  width: 430px;
  min-height: 330px;
  background-color: #FFFFFF;
  box-shadow: 0px 0px 25px rgba(0, 0, 0, 0.15);
  border-radius: 10px;
  position: relative;
  cursor: default;
}

@media screen and (max-width: 767px) {

  .popup__container {
    width: 282px;
    min-height: 322px;
  }
}
